#fileTree {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: start;
	    align-items: flex-start;	
	width: 30%;
	height: 100%;
	float: right;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

#fileTree>.title,
#copyNodetree {
	margin-left: 10px;
}

#fileTree::before {
	position: absolute;
	content: '';
	border-left: 1px solid #f2f2f2;
	height: 100%;
	left: -8%;
}

#copyNodetree {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	padding-left: 0px;
}

/* File tree item */
.tree-item {
	overflow: auto;
	padding: 2px;
	margin-left: 10px;
}
.tree-item>i {
	display: block;
	font-size: 20px;
}
.tree-item>span {
	font-size: 10px;
}
.tree-location {
	border: 1px solid blue;
}


