@import url('fileManager.css');
@import url('fileTree.css');

#content {	
	width: 90%;
	height: 90vh;
	margin: 5vh auto;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.title {
	border-left: 3px solid #000099 !important;
	color: #0000ff;
	border: 1px solid #f2f2f2;
	width: 90%;
	padding: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

/* Icons */
.icon {
	color: #ffff00;
	cursor: pointer;
	padding-left: 10px;
}
.icon:hover {
	color: #bfbfbf;
}
/* - Folder icon*/
.fa-folder,
.fa-file {
	color: #ffff00;
	font-size: 40px;
}

/* Buttons */
button[type='button'] {
	background-color: #ffff00;
	color: blue;
	border: none;
	margin-right: 10px;
	cursor: pointer;
}
button[type='button']:hover {
	color: #bfbfbf;
}


