#fileManager {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	width: 70%;
	height: 100%;
	float: left;
}

.fileManager-path {
	width: 90%;
}

/* Manager */
#manager {
	position: relative;
	background-color: #f2f2f2;
	border-radius: 4px;
	width: 90%;
	margin-top: 20px;
	overflow: hidden;
	max-width: 600px;
	min-width: 450px;
	height: 400px;
}

/* - Manager options */
.manager-options {
	display: table;
	background-color: #0000ff;
	width: 100%;
	height: 35px;
}
.manager-options-folder {
	display: table-cell;
	color: #ffffff;	
	height: inherit;
	vertical-align: middle;	
	padding-left: 20px;
	font-weight: bold;
}
.manager-options-layout {
	display: table-cell;
	height: inherit;
	vertical-align: middle;	
	padding-right: 10px;
	text-align: right;
}

/* - Manager tools */
.manager-tools {
	position: relative;
	background-color: #0000ff;
	padding-left: 20px;
	padding-bottom: 10px;
}

/*  - Manager content */
.manager-content {
	width: 100%;
	height: 250px;
	overflow: auto;
}

.manager-content-items {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: start;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	list-style-type: none;
	padding-left: 0px;
	margin: 10px;
}

/* Tile layout */
.collectionItem {
	width: 100px;
	height: 100px;
	text-align: center;
	cursor: pointer;
	-webkit-box-sizing: border-block;
	        box-sizing: border-block;
}
.parentFolder {
	border: 1px solid #ffff00;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-align: center;
	    align-items: center;
}
.collectionItem-name {
	margin: 10px;
}
.collectionItem:hover {
	border: 1px solid #bfbfbf;
}
.selected {
	border: 1px solid blue !important;
}

/* List layout */
.collectionItem-list {
	display: -ms-inline-flexbox;
	display: inline-flex;
	background-color: #bfbfbf;
	-ms-flex-pack: start;
	    justify-content: start;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	height: 40px;
	margin-bottom: 10px;
	cursor: pointer;
}
.collectionItem-list>i {
	margin-left: 20px;
}
.collectionItem-list>p {
	margin-left: 20px;
}
.collectionItem-list.file>i {
	font-size: 25px;
}
.collectionItem-list.parentFolder {
	border: none;
	padding-left: 20px;
}
.collectionItem-list:hover {
	background-color: lightgrey;
}

/* Manager favourites */
.manager-favourites {
	position: absolute;
	background-color: blue;
	bottom: 0px;
	width: 100%;
	height: 70px;
}
.manager-favourites>p {
	position: absolute;
	color: #ffffff;
	margin-left: 20px;
	top: -15px;
	font-weight: bold;
}
.manager-favourites-list {
	padding-left: 0px;
	height: 40px;
	margin-top: 20px;
	overflow: auto;
}

.manager-favourites-list-item {
	display: inline-block;
	height: 100%;
	margin-left: 20px;
}
.manager-favourites-list-item>i{
	font-size: 25px;
}
.manager-favourites-list-item>p{
	color: #ffffff;
	margin: 0px;
	font-size: 15px;
}

.manager-favourites-list-parentFolder {
	position: absolute;
	display: block;
	left: 100px;
	top: 0px;
}
.manager-favourites-list-parentFolder>i {
	font-size: 20px;
}

/* Upload */
.manager-tools-upload {
	position: absolute;
	display: none;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	background-color: white;	
	width: calc(100% - 25px);	
	left: 10px;		
}
.implement {
	display: -ms-inline-flexbox !important;
	display: inline-flex !important;
}

/* Progress bar */
.progressBar {
	position: absolute;	
	background-color: green;
	height: 5px;
	width: 0%;
	top: 64px;
	-webkit-transition: all 3s;
	-o-transition: all 3s;
	transition: all 3s;
}
.long {
	width: 100%;	
}

/* Search */
.manager-tools-search {
	position: absolute;
	display: none;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	background-color: white;	
	width: calc(100% - 25px);	
	left: 10px;		
}
.manager-tools-search>input {
	margin: 1px;
	margin-right: 10px;
}
.manager-tools-search-result {
	position: absolute;
	background-color: #ffffff;
	display: none;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 90%;
	top: 26px;
	padding: 10px;
}
.manager-tools-search-result>span {
	margin: 0px 10px;
}
.visible {
	display: -ms-inline-flexbox !important;
	display: inline-flex !important;
}


